.container {
  margin: 20px 0 0 0;
  padding: 0 0 60px 0;
}

.title {
  margin: 0 0 15px 0;
  font-size: 22px;
  font-weight: 500;
  text-align: center;
}


.form {
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.formItem {
  border: 1px;
}

.formItem input {
  font-size: 16px;
  padding: 7px 5px;

  border: 1px solid hsl(0, 0%, 80%);
  outline: solid 2px transparent;
  border-radius: 10px;

  transition: all 0.1s ease;
}

.formItem input:focus {
  outline: solid 2px #2684ff;
  border-color: transparent;
}

.formItem input:not(:focus):hover {
  border-color: hsl(0, 0%, 70%);
}

.formItem._number {
  display: flex;
  align-items: center;
  gap: 30px;
}

.formItem._radio {
  align-items: start;
}

.formItem._number .formItemTitle {
  flex: 0 1 38%;
}

.formItemTitle {
  font-size: 16px;
}

.formItemInput._cb {
  display: flex;
  align-items: center;
}

.formItemInput._radio {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.formItemInput._radio>div {
  display: flex;
  gap: 8px;
}

.formButtons {
  display: flex;
  gap: 20px;
}

.formButtons button {
  flex: 0 1 48%;
  padding: 10px 10px;
  border-radius: 15px;
  background-color: rgb(91 137 255);

  font-size: 18px;
  font-weight: 500;
}