.header {
  padding: 30px 0;
  box-shadow: 0 0 42px -20px black;
  border-radius: 0 0 10px 10px;
}

.menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 35px;
}

.munuItem {}

.munuItem a {
  white-space: nowrap;
  color: #020202;
  font-size: 18px;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
}

.munuItem a::before {
  content: "";
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #020202;

  opacity: 0;
  transition: opacity 0.3s ease;
}

.munuItem a:hover::before {
  opacity: 1;
}