.table  {
  margin: 100px auto;
  font-family: 'PTMono';
}

.table td {
  padding: 15px 30px;
  font-size: 16px;
  max-width: 400px;

  vertical-align: top;
}


.head {
  font-size: 16px;
  font-weight: 600;

  position: relative;
}

.head::after {
  content: "";
  position: absolute;
  height: 2px;
  bottom: 0;
  width: calc(100% - 60px);
  left: 50%;
  transform: translateX(-50%);
  background-color: rgb(204, 204, 204);
}


._number {
  text-align: right;
}